body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

nav {
    box-shadow: 0 .25rem rgb(94, 93, 93, .3);
}

nav .brand-logo {
    position: relative;
    margin: .8rem;
    font-size: 2.25rem;
}

.user-guess {
    font-size: 1.5rem;
}

.score-container {
    margin-right: 1rem;
}

.score-text {
    margin-right: 1rem;
    font-size: 1.4rem;
}

.wrong-guess {
    -webkit-animation: red-text .5s 1;
            animation: red-text .5s 1;
}

@-webkit-keyframes green-text {
0% { color: green; }
100% { color: white; }
}

@keyframes green-text {
0% { color: green; }
100% { color: white; }
}

.right-guess {
    -webkit-animation: green-text .5s 1;
            animation: green-text .5s 1;
}

@-webkit-keyframes red-text {
    0% { color: red; }
    100% { color: white; }
    }

@keyframes red-text {
    0% { color: red; }
    100% { color: white; }
    }
.header {
    margin-top: 0;
    box-shadow: 0px 0px 0px 0px rgba(0,0,0,0);
}
.card {
    width: 20%;
    height: 20%;
    margin: 1rem .5rem;
}

.character-portrait {
    padding: .3rem;
    padding-bottom: 0;
    border-radius: .75rem;
    cursor: pointer;
    width: 180px;
    height: 180px;
}

.shake {
    /* Start the shake animation and make the animation last for 0.5 seconds */
    -webkit-animation: shake 0.2s;
            animation: shake 0.2s; 
  
    /* When the animation is finished, start again */
    -webkit-animation-iteration-count: 3;
            animation-iteration-count: 3; 
  }
  
  @-webkit-keyframes shake {
    0% { -webkit-transform: translate(1px, 0px); transform: translate(1px, 0px); }
    25% { -webkit-transform: translate(-2px, 0px); transform: translate(-2px, 0px); }
    50% { -webkit-transform: translate(2px, 0px); transform: translate(2px, 0px); }
    75% { -webkit-transform: translate(-1px, 0px); transform: translate(-1px, 0px); }
  }
  
  @keyframes shake {
    0% { -webkit-transform: translate(1px, 0px); transform: translate(1px, 0px); }
    25% { -webkit-transform: translate(-2px, 0px); transform: translate(-2px, 0px); }
    50% { -webkit-transform: translate(2px, 0px); transform: translate(2px, 0px); }
    75% { -webkit-transform: translate(-1px, 0px); transform: translate(-1px, 0px); }
  }
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.flex-row {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
}
